html {
  //font-size: 62.5% !important;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input:focus,
button:focus,
a:focus button:active {
  outline: 0;
}

/* custom list */
ul.custom-list {
  li {
    width: 33.33%;
    position: relative;

    &.active-radio {
      > div {
        background: rgb(255, 246, 197);
      }
    }
    > div {
      cursor: pointer;
      height: auto;
      background: rgb(249, 249, 249);
      border-radius: 12px;
      padding: 0px 0px 12px 0px;
      transition: 0.1s ease;

      &:hover {
        background: rgb(255, 246, 197);
      }
    }
  }
}
/* Radio Button Styles */
div.custom-radio-button {
  cursor: pointer;
  text-align: center;
  svg {
    margin: 0 auto;
    width: 24px;
  }

  input[type="radio"] {
    display: block;
    position: absolute;
    top: 0px;
    opacity: 0;
  }
  p {
    font-size: 11px;
    font-weight: 400;
    text-transform: uppercase;
  }
  label {
    background: none;
    display: block;
    margin: 0px;
    color: #999;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    cursor: pointer;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
  -webkit-appearance: none;
  appearance: none;
  -moz-appearance: none;
}

/* Temporary styles*/
header {
  nav {
    ul {
      padding: 0px;
      margin: 0px;
      li {
        margin: 0px 12px;
        display: inline-block;
        list-style: none;
      }
    }
  }
}




